import {Avatar, Grid, List, Paper, Typography} from "@mui/material";
import {DivvyUSDFormat, DivvyCryptoFormat} from "../utils";
import {useSelector} from "react-redux";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import {BNBIcon} from "../utils";
import LocalAtmIcon from '@mui/icons-material/LocalAtm';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import StatDetail from "./StatDetail";
import {useEffect, useState} from "react";

export default function DivvyFundStats(props) {

    const {project} = props;
    const {bnbPrice, projectsMap, wallet} = useSelector(state => state.divvy);
    const {stats} = projectsMap[project.contract_address];
    const reflectionsBalance = project.floor === 0 ? stats.divvyBalance - (stats.supply * .01) : stats.divvyBalance - project.divvyFund;

    // sponsor check
    const [viewable, setViewable] = useState(false);
    const [tokensNeeded, setTokensNeeded] = useState(0);
    const sponsorToken = '0xe9490A983292bD844074Ac3e83c3cb408f578b61';
    const sponsorMin = 10;// in USD
    useEffect(() => {
        setViewable(false);
        setTokensNeeded(sponsorMin / bnbPrice / projectsMap[sponsorToken]?.stats.price)
        if (wallet.balances[sponsorToken] && projectsMap[sponsorToken].stats.price && bnbPrice) {

            const userVal = wallet.balances[sponsorToken] * projectsMap[sponsorToken].stats.price * bnbPrice;

            if (userVal >= sponsorMin) {
                setViewable(true);
            } else {
                setViewable(false);
            }
        }
    }, [wallet.balances[sponsorToken], projectsMap[sponsorToken]?.stats.price, bnbPrice])

    return <Paper elevation={0}>
        <Grid container spacing={2}>
            <Grid item>
                <Avatar><img
                    src="https://fegexchange.github.io/assets/blockchains/smartchain/assets/0xe9490A983292bD844074Ac3e83c3cb408f578b61/logo.png"
                    width={40} alt="Divvy Logo"/></Avatar>
            </Grid>
            <Grid item>
                <Typography variant="h6">Divvy Fund</Typography>
            </Grid>
        </Grid>
        {
            viewable ? <Grid container>
                    <Grid item xs={12} md={6}>
                        <List disablePadding>
                            <StatDetail
                                avatar={<AccountBalanceWalletIcon color="primary"/>}
                                primary={`${DivvyCryptoFormat(stats.divvyBalance)} ${project.symbol}`}
                                secondary="Balance"
                            />
                            <StatDetail
                                avatar={<BNBIcon color="#872eff"/>}
                                primary={`${DivvyCryptoFormat(stats.divvyBalance * stats.price)} BNB`}
                                secondary="Value BNB"
                            />
                            <StatDetail
                                avatar={<AttachMoneyIcon color="primary"/>}
                                primary={DivvyUSDFormat(stats.divvyBalance * (stats.price * bnbPrice))}
                                secondary="Value USD"
                            />
                        </List>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <List disablePadding>
                            <StatDetail
                                avatar={<LocalAtmIcon color="primary"/>}
                                primary={`${DivvyCryptoFormat(reflectionsBalance)} ${project.symbol}`}
                                secondary="Reflections"
                            />
                            <StatDetail
                                avatar={<BNBIcon color="#872eff"/>}
                                primary={`${DivvyCryptoFormat(reflectionsBalance * stats.price)} BNB`}
                                secondary="Reflections Value BNB"
                            />
                            <StatDetail
                                avatar={<AttachMoneyIcon color="primary"/>}
                                primary={`${DivvyUSDFormat((reflectionsBalance) * (stats.price * bnbPrice))}`}
                                secondary="Reflections Value USD"
                            />
                        </List>
                    </Grid>
                </Grid>
                :
                wallet.address ?
                    <Grid container>
                        <Grid item xs={12} style={{marginTop: 20}}>
                            <Typography textAlign="center">To access Divvy Fund, you need to
                                hold <br/><strong>{DivvyUSDFormat(sponsorMin)}</strong> worth of DIVVY.<br/><Typography
                                    textAlign="center"
                                    variant="caption">{DivvyCryptoFormat(tokensNeeded)} DIVVY</Typography></Typography>


                        </Grid>
                    </Grid>
                    :
                    <Typography textAlign="center" style={{marginTop: 20}}>Connect your wallet to unlock this
                        section.</Typography>
        }
    </Paper>
}